import cmdIcon from '../cmd-icon/cmd-icon.vue'

export default {
  name: 'cmd-cell-item',

  components: {
    cmdIcon
  },

  props: {
    /**
     * 标题
     */
    title: {
      type: String,
      default: ''
    },
    /**
     * 标题描述文本
     */
    brief: {
      type: String,
      default: ''
    },
    /**
     * 附加文本
     */
    addon: {
      type: String,
      default: ''
    },
    /**
     * 附加文本2 item下文字说明
     */
    addon2: {
      type: String,
      default: ''
    },
    /**
     * 动作箭头标识
     */
    arrow: {
      type: Boolean,
      default: false
    },
    /**
     * 是否禁用项
     */
    disabled: {
      type: Boolean,
      default: false
    },
    /**
     * 无边框项
     */
    noBorder: {
      type: Boolean,
      default: false
    },
    /**
     * 显示开关
     */
    showSwitch: {
      type: Boolean,
      default: false
    },
    /**
     * 开关状态 是否被选中
     */
    switchState: {
      type: Boolean,
      default: false
    },
    /**
     * 开关颜色
     */
    switchColor: {
      type: String,
      default: ''
    },
    /**
     * 插槽左 不可同时打开两个插槽
     */
    slotLeft: {
      type: Boolean,
      default: false
    },
    /**
     * 插槽右 不可再使用switch addon
     */
    slotRight: {
      type: Boolean,
      default: false
    },
    /**
     * 指定按钮按下去的样式类
     */
    hoverClass: {
      type: String,
      default: 'cmd-cell-item-hover'
    }
  },

  methods: {
    /**
     * item项点击事件
     */
    $_click(e) {
      if (!this.disabled) {
        this.$emit('click', e)
      }
    },
    /**
     * 开关触发改变事件
     */
    $_switch(e) {
      if (!this.disabled) {
        this.$emit('switch', e)
      }
    }
  }

}
